html {
	min-width: 100vw;
}

body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-image: url(/battleship/static/media/blue-ocean-sea-5412.ece3f649.jpg);
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
	text-align: center;
}

.App-header {
	background-color: #282c34;
	min-height: 7vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(0.65em + 2.5vmin);
	color: white;
}

.scoreboard {
	position: relative;
	margin: 0.6em 0 0 0;
	left: 68%;
	font-weight: bold;
	text-align: left;
	width: 14rem;
	text-shadow: 0 0 5px lightblue;
}

#commentary-display {
	position: relative;
	width: 22rem;
	left: 68%;
	top: 10rem;
	font-weight: bold;
	text-align: left;
	text-shadow: 0 0 5px lightblue;
}

ul {
	padding: 0;
	margin: 0;
}

.btn {
	/* Transitions */
	-webkit-transition: 0.1s ease-out;
	transition: 0.1s ease-out;
	/* Shadows */
	box-shadow: inset 0 0 5px 0 rgba(255, 255, 255, 0.5);
	-moz-box-shadow: inset 0 0 5px 0 rgba(255, 255, 255, 0.5);
	-ms-box-shadow: inset 0 0 5px 0 rgba(255, 255, 255, 0.5);
	-webkit-box-shadow: inset 0 0 5px 0 rgba(255, 255, 255, 0.5);
	-khtml-box-shadow: inset 0 0 5px 0 rgba(255, 255, 255, 0.5);
	-o-box-shadow: inset 0 0 5px 0 rgba(255, 255, 255, 0.5);
	/*[{- Border radius -}]*/
	border-radius: 5rem;
	-moz-border-radius: 5rem;
	-webkit-border-radius: 5rem;
	-ms-border-radius: 5rem;
	-khtml-border-radius: 5rem;
	-o-border-radius: 5rem;
	/* Gradient */
	background-image: -webkit-linear-gradient(top, transparent, rgba(0, 0, 0, 0.2));
	background-image: -khtml-linear-gradient(top, transparent, rgba(0, 0, 0, 0.2));
	background-image: linear-gradient(top, transparent, rgba(0, 0, 0, 0.2));
	cursor: crosshair;
}

.btn:visited {
	border: 2px inset black;
}

#human-board > .btn {
	padding: 0px 0.125rem;
	border: 1px solid black;
	/* background-color: #bbb; */
	color: black;
	font-size: 0.8rem;
	line-height: 1.6rem;
}

#computer-board > .btn {
	line-height: 1.2rem;
}

#human-board > .board-squares.btn:hover {
	color: white;
	background-color: #999;
	/*{Gradient}*/
	background-image: -webkit-radial-gradient(circle, black, white);
	background-image: radial-gradient(circle, black, white);
	/* background-image: -moz-linear-gradient(top, transparent, rgba(0, 0, 0, 0.3)); */
	/* background-image: -webkit-linear-gradient(top, transparent, rgba(0, 0, 0, 0.3)); */
	/* background-image: -ms-linear-gradient(top, transparent, rgba(0, 0, 0, 0.3)); */
	/* background-image: -khtml-linear-gradient(top, transparent, rgba(0, 0, 0, 0.3)); */
	/* background-image: -o-linear-gradient(top, transparent, rgba(0, 0, 0, 0.3)); */
	/* background-image: linear-gradient(top, transparent, rgba(0, 0, 0, 0.3)); */
}

#human-board > .board-squares.btn:active {
	color: #666;
	border: 1px solid #999;
	/*{Shadow}*/
	box-shadow: inset 0 0 5px 0 #333;
	-moz-box-shadow: inset 0 0 5px 0 #333;
	-webkit-box-shadow: inset 0 1px 2px 0 #333, 0 1px 1px 0 #fff;
}

#human-board > .hit.btn {
	color: black;
	background-color: red;
	text-shadow: 0 0 4px white;
	padding: 0px 0.125rem;
	background-image: -webkit-radial-gradient(circle closest-side, red, black);
	background-image: radial-gradient(circle closest-side, red, black);
}

#human-board > .miss.btn {
	color: black;
	background-color: white;
	padding: 0px 0.125rem;
	background-image: -webkit-radial-gradient(circle closest-side, white, darkgrey, black);
	background-image: radial-gradient(circle closest-side, white, darkgrey, black);
}

#computer-board > .board-squares.btn {
	/* background-color: #bbbbbb; */
	font-size: 0.6em;
	letter-spacing: 0.5px;
	padding: 0px 0.125rem;
	border: 1px solid black;
	pointer-events: none;
}

#computer-board > .hit.btn {
	color: black;
	background-color: red;
	padding: 0px 0.125rem;
	box-shadow: inset 3px 3px 8px black;
	font-size: 0.6em;
	letter-spacing: 0.5px;
}

#computer-board > .miss.btn {
	color: black;
	background-color: white;
	padding: 0px 0.125rem;
	box-shadow: inset 3px 3px 8px black;
	letter-spacing: 0.5px;
	font-size: 0.6em;
}

.playerShips.btn {
	background-color: black;
	font-size: 0.6em;
	letter-spacing: 0.5px;
	padding: 0px 0.125rem;
	border: 1px solid black;
	color: white;
	border-radius: 0.625rem;
}

.disabled.btn {
	pointer-events: none;
}

.btn {
	/* transition */
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
	margin: 0;
}

#human-board {
	width: 22rem;
	height: 22rem;
	display: grid;
	grid-gap: 0.2rem;
	padding: 0.2rem;
	position: relative;
	grid-template-columns: repeat(10, 1fr);
	font-weight: 500;
	cursor: crosshair;
}

#computer-board {
	width: 16rem;
	height: 16rem;
	display: grid;
	grid-gap: 0.2rem;
	padding: 0.2rem;
	position: relative;
	grid-template-columns: repeat(10, 1fr);
}

.board-header {
	margin: 0;
	text-shadow: 0 0 3px lightblue;
}

.play-area {
	position: fixed;
	width: 80%;
	min-height: 100%;
	top: 4rem;
	left: 8rem;
	display: inline-block;
}

#human-board-wrapper {
	float: right;
	margin: 8% 32% 0 0;
}

#computer-board-wrapper {
	float: left;
	margin: 1% 0 0 0;
}

